import React, { useEffect, useState } from 'react';
import { useParams, useLocation, useNavigate } from 'react-router-dom';
import useBookingFlow from '../hooks/useBookingFlow';
import CreditAlert from './shared/CreditAlert';
import SessionTimer from './shared/SessionTimer';
import { formatDate } from '../services/api';

import { getUserSession, clearUserSession } from '../utils/auth';
const BookingForm = () => {
  const { mockExamId } = useParams();
  const location = useLocation();
  const navigate = useNavigate();

  const mockType = location.state?.mockType || 'Situational Judgment';
  const examDate = location.state?.examDate || '';
  const examLocation = location.state?.location || 'Mississauga';

  // Get user session data
  const [userSession, setUserSession] = useState(null);

  const {
    step,
    bookingData,
    error,
    loading,
    validationErrors,
    verifyCredits,
    submitBooking,
    updateBookingData,
    clearError,
    canProceed,
  } = useBookingFlow(mockExamId, mockType);

  const [dominantHand, setDominantHand] = useState(null);

  // Load user session on component mount
  useEffect(() => {
    const userData = getUserSession();
    if (userData) {
      setUserSession(userData);

      // Auto-verify credits using session data
      verifyCredits(userData.studentId, userData.email);
    } else {
      // No session found, redirect to login
      navigate('/login');
    }
  }, [navigate, verifyCredits]);

  // Update booking data with exam details
  useEffect(() => {
    updateBookingData({
      mockExamId,
      mockType,
      examDate,
    });
  }, [mockExamId, mockType, examDate, updateBookingData]);

  const handleSubmitBooking = async (e) => {
    e.preventDefault();

    if (dominantHand === null) {
      alert('Please select your dominant hand');
      return;
    }

    // Update booking data with all required information from session
    updateBookingData({
      name: userSession?.studentName || 'Student',
      studentId: userSession?.studentId || '',
      email: userSession?.email || '',
      dominantHand: dominantHand
    });

    const result = await submitBooking();
    if (result) {

      // Use a fallback booking ID if the one from the result is undefined
      const fallbackBookingId = result.bookingId || `booking-${Date.now()}`;

      navigate(`/booking/confirmation/${encodeURIComponent(fallbackBookingId)}`, {
        state: { bookingData: result }
      });
    }
  };

  const handleSessionExpire = () => {
    alert('Your session has expired. Please start over.');
    clearUserSession();
    navigate('/login');
  };

  if (!userSession) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center">
        <div className="text-center">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-primary-600 mb-4"></div>
          <p className="text-gray-600">Loading...</p>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gray-50">
      <SessionTimer
        expiryMinutes={15}
        onExpire={handleSessionExpire}
        onExtend={() => console.log('Session extended')}
      />

      <div className="container-app py-12 max-w-2xl">
        {/* Header */}
        <div className="mb-8">
          <button
            onClick={() => navigate(-1)}
            className="inline-flex items-center text-sm text-gray-600 hover:text-gray-900 mb-4"
          >
            <svg className="w-4 h-4 mr-1" fill="currentColor" viewBox="0 0 20 20">
              <path fillRule="evenodd" d="M9.707 16.707a1 1 0 01-1.414 0l-6-6a1 1 0 010-1.414l6-6a1 1 0 011.414 1.414L5.414 9H17a1 1 0 110 2H5.414l4.293 4.293a1 1 0 010 1.414z" clipRule="evenodd" />
            </svg>
            Back to sessions
          </button>

          {/* User Info */}
          <div className="card bg-primary-50 border-primary-200 mb-6">
            <h2 className="text-lg font-semibold text-primary-900 mb-2">Logged in as</h2>
            <div className="space-y-1 text-sm text-primary-700">
              <p><strong>Name:</strong> {userSession.studentName}</p>
              <p><strong>Student ID:</strong> {userSession.studentId}</p>
              <p><strong>Email:</strong> {userSession.email}</p>
            </div>
          </div>

          {/* Exam Details Card */}
          <div className="card bg-primary-50 border-primary-200 mb-6">
            <h2 className="text-lg font-semibold text-primary-900 mb-2">Selected Exam</h2>
            <div className="space-y-1 text-sm text-primary-700">
              <p><strong>Type:</strong> {mockType}</p>
              <p><strong>Date:</strong> {formatDate(examDate)}</p>
              <p><strong>Location:</strong> {examLocation}</p>
            </div>
          </div>
        </div>

        {/* Error Display */}
        {error && (
          <div className="mb-6 bg-error-50 border border-error-200 rounded-lg p-4">
            <div className="flex">
              <div className="flex-shrink-0">
                <svg className="h-5 w-5 text-error-400" viewBox="0 0 20 20" fill="currentColor">
                  <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zM8.707 7.293a1 1 0 00-1.414 1.414L8.586 10l-1.293 1.293a1 1 0 101.414 1.414L10 11.414l1.293 1.293a1 1 0 001.414-1.414L11.414 10l1.293-1.293a1 1 0 00-1.414-1.414L10 8.586 8.707 7.293z" clipRule="evenodd" />
                </svg>
              </div>
              <div className="ml-3">
                <h3 className="text-sm font-medium text-error-800">
                  Error
                </h3>
                <div className="mt-2 text-sm text-error-700">
                  {error}
                </div>
              </div>
              <button
                onClick={clearError}
                className="ml-auto flex-shrink-0 text-error-400 hover:text-error-500"
              >
                <span className="sr-only">Close</span>
                <svg className="h-5 w-5" viewBox="0 0 20 20" fill="currentColor">
                  <path fillRule="evenodd" d="M4.293 4.293a1 1 0 011.414 0L10 8.586l4.293-4.293a1 1 0 111.414 1.414L11.414 10l4.293 4.293a1 1 0 01-1.414 1.414L10 11.414l-4.293 4.293a1 1 0 01-1.414-1.414L8.586 10 4.293 5.707a1 1 0 010-1.414z" clipRule="evenodd" />
                </svg>
              </button>
            </div>
          </div>
        )}

        {/* Credit validation and booking flow */}
        {step === 'verify' && (
          <div className="card animate-fade-in">
            <div className="text-center">
              <div className="inline-block animate-spin rounded-full h-12 w-12 border-b-2 border-primary-600 mb-4"></div>
              <p className="text-gray-600">Verifying your credits...</p>
            </div>
          </div>
        )}

        {/* Booking Details - after credits verified */}
        {step === 'details' && (
          <div className="space-y-6 animate-fade-in">
            {/* Credit Alert */}
            <CreditAlert
              credits={bookingData.credits}
              creditBreakdown={bookingData.creditBreakdown}
              mockType={mockType}
              variant={bookingData.credits > 0 ? 'success' : 'error'}
            />

            {/* Booking Form */}
            <div className="card">
              <h2 className="text-xl font-semibold text-gray-900 mb-6">
                Complete Your Booking
              </h2>

              <form onSubmit={handleSubmitBooking} className="space-y-4">
                <div>
                  <label className="label">
                    Dominant Hand *
                  </label>
                  <div className="mt-2 space-y-2">
                    <label className="inline-flex items-center">
                      <input
                        type="radio"
                        name="dominantHand"
                        value="true"
                        checked={dominantHand === true}
                        onChange={() => setDominantHand(true)}
                        className="form-radio h-4 w-4 text-primary-600"
                        required
                      />
                      <span className="ml-2">Right-handed</span>
                    </label>
                    <label className="inline-flex items-center ml-6">
                      <input
                        type="radio"
                        name="dominantHand"
                        value="false"
                        checked={dominantHand === false}
                        onChange={() => setDominantHand(false)}
                        className="form-radio h-4 w-4 text-primary-600"
                        required
                      />
                      <span className="ml-2">Left-handed</span>
                    </label>
                  </div>
                </div>

                <div className="pt-4">
                  <button
                    type="submit"
                    disabled={loading || !canProceed || dominantHand === null}
                    className="btn-primary w-full"
                  >
                    {loading ? (
                      <>
                        <svg className="animate-spin -ml-1 mr-3 h-5 w-5 text-white" fill="none" viewBox="0 0 24 24">
                          <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle>
                          <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
                        </svg>
                        Creating Booking...
                      </>
                    ) : (
                      'Confirm Booking'
                    )}
                  </button>
                </div>
              </form>
            </div>
          </div>
        )}

        {/* Confirming State */}
        {step === 'confirming' && (
          <div className="card text-center">
            <div className="inline-block animate-spin rounded-full h-12 w-12 border-b-2 border-primary-600 mb-4"></div>
            <p className="text-gray-600">Processing your booking...</p>
          </div>
        )}
      </div>
    </div>
  );
};

export default BookingForm;