import React from 'react';
import { format } from 'date-fns';
import { formatDate, formatTime } from '../../services/api';
import CapacityBadge from './CapacityBadge';

const SessionDrawer = ({ isOpen, onClose, selectedDate, sessions, onSelectSession }) => {
  if (!isOpen) return null;

  const LocationIcon = () => (
    <svg className="w-4 h-4" fill="currentColor" viewBox="0 0 20 20">
      <path fillRule="evenodd" d="M5.05 4.05a7 7 0 119.9 9.9L10 18.9l-4.95-4.95a7 7 0 010-9.9zM10 11a2 2 0 100-4 2 2 0 000 4z" clipRule="evenodd" />
    </svg>
  );

  const CalendarIcon = () => (
    <svg className="w-4 h-4" fill="currentColor" viewBox="0 0 20 20">
      <path fillRule="evenodd" d="M6 2a1 1 0 00-1 1v1H4a2 2 0 00-2 2v10a2 2 0 002 2h12a2 2 0 002-2V6a2 2 0 00-2-2h-1V3a1 1 0 10-2 0v1H7V3a1 1 0 00-1-1zm0 5a1 1 0 000 2h8a1 1 0 100-2H6z" clipRule="evenodd" />
    </svg>
  );

  return (
    <>
      {/* Backdrop */}
      <div
        className="fixed inset-0 bg-black bg-opacity-50 z-40 transition-opacity"
        onClick={onClose}
      />

      {/* Drawer */}
      <div className="fixed inset-x-0 bottom-0 z-50 bg-white rounded-t-lg shadow-xl transform transition-transform max-h-[80vh] overflow-hidden">
        <div className="flex flex-col h-full">
          {/* Header */}
          <div className="flex items-center justify-between p-4 border-b">
            <div>
              <h3 className="text-lg font-semibold text-gray-900">
                Available Sessions
              </h3>
              <p className="text-sm text-gray-600">
                {selectedDate && format(selectedDate, 'EEEE, MMMM d, yyyy')}
              </p>
            </div>
            <button
              onClick={onClose}
              className="p-2 hover:bg-gray-100 rounded-md transition-colors"
              aria-label="Close drawer"
            >
              <svg className="w-5 h-5 text-gray-500" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
              </svg>
            </button>
          </div>

          {/* Sessions List */}
          <div className="flex-1 overflow-y-auto p-4 space-y-3">
            {sessions && sessions.length > 0 ? (
              sessions.map((session) => (
                <div
                  key={session.mock_exam_id}
                  className={`border rounded-lg p-4 transition-all duration-200 ${
                    session.available_slots > 0
                      ? 'hover:border-primary-200 hover:shadow-md cursor-pointer bg-white'
                      : 'bg-gray-50 opacity-75'
                  }`}
                  onClick={() => session.available_slots > 0 && onSelectSession(session)}
                >
                  <div className="flex items-center justify-between">
                    <div className="flex-1">
                      <div className="flex items-center gap-3 mb-2">
                        <CapacityBadge
                          availableSlots={session.available_slots}
                          capacity={session.capacity}
                        />
                      </div>

                      <div className="space-y-2 text-sm text-gray-600">
                        <div className="flex items-center gap-2">
                          <CalendarIcon />
                          <span className="font-medium">
                            {formatTime(session.exam_date) || '9:00 AM'}
                          </span>
                        </div>
                        <div className="flex items-center gap-2">
                          <LocationIcon />
                          <span>{session.location}</span>
                        </div>
                        <div className="text-xs text-gray-500">
                          Capacity: {session.capacity - session.available_slots}/{session.capacity} booked
                        </div>
                      </div>
                    </div>

                    <button
                      className={`ml-4 px-4 py-2 rounded-md text-sm font-medium transition-colors ${
                        session.available_slots > 0
                          ? 'bg-primary-600 text-white hover:bg-primary-700'
                          : 'bg-gray-200 text-gray-500 cursor-not-allowed'
                      }`}
                      disabled={session.available_slots === 0}
                      onClick={(e) => {
                        e.stopPropagation();
                        if (session.available_slots > 0) {
                          onSelectSession(session);
                        }
                      }}
                    >
                      {session.available_slots > 0 ? 'Select' : 'Full'}
                    </button>
                  </div>
                </div>
              ))
            ) : (
              <div className="text-center py-8 text-gray-500">
                <p>No sessions available for this date.</p>
              </div>
            )}
          </div>

          {/* Footer */}
          <div className="border-t p-4 bg-gray-50">
            <button
              onClick={onClose}
              className="w-full py-2 px-4 border border-gray-300 rounded-md text-sm font-medium text-gray-700 hover:bg-gray-100 transition-colors"
            >
              Close
            </button>
          </div>
        </div>
      </div>
    </>
  );
};

export default SessionDrawer;