import React from 'react';

const CapacityBadge = ({ availableSlots, capacity, size = 'normal' }) => {
  const percentage = capacity > 0 ? (availableSlots / capacity) * 100 : 0;

  let colorClass = '';
  let text = '';

  if (availableSlots === 0) {
    colorClass = 'bg-error-100 text-error-800';
    text = 'Full';
  } else if (percentage <= 20) {
    colorClass = 'bg-warning-100 text-warning-800';
    text = availableSlots === 1 ? '1 slot left' : `${availableSlots} slots left`;
  } else {
    colorClass = 'bg-success-100 text-success-800';
    text = `${availableSlots} slots available`;
  }

  const sizeClass = size === 'large'
    ? 'px-3 py-1.5 text-sm font-semibold'
    : 'px-2.5 py-0.5 text-xs font-medium';

  return (
    <span className={`inline-flex items-center rounded-full ${sizeClass} ${colorClass}`}>
      {text}
    </span>
  );
};

export default CapacityBadge;