import React, { useState, useMemo } from 'react';
import { format, startOfMonth, endOfMonth, eachDayOfInterval, isSameDay, isToday, isBefore, startOfDay } from 'date-fns';

const CalendarView = ({ exams, onDateSelect, onExamSelect }) => {
  const [currentDate, setCurrentDate] = useState(new Date());

  // Group exams by date
  const examsByDate = useMemo(() => {
    const grouped = {};
    exams.forEach(exam => {
      const dateKey = format(new Date(exam.exam_date), 'yyyy-MM-dd');
      if (!grouped[dateKey]) {
        grouped[dateKey] = [];
      }
      grouped[dateKey].push(exam);
    });
    return grouped;
  }, [exams]);

  // Generate calendar days for current month
  const calendarDays = useMemo(() => {
    const start = startOfMonth(currentDate);
    const end = endOfMonth(currentDate);
    const days = eachDayOfInterval({ start, end });

    // Add empty cells for the start of the month
    const startDay = start.getDay(); // 0 = Sunday
    const emptyDays = Array(startDay).fill(null);

    return [...emptyDays, ...days];
  }, [currentDate]);

  const navigateMonth = (direction) => {
    setCurrentDate(prev => {
      const newDate = new Date(prev);
      newDate.setMonth(prev.getMonth() + direction);
      return newDate;
    });
  };

  const handleDateClick = (date) => {
    if (!date || isBefore(date, startOfDay(new Date()))) return;

    const dateKey = format(date, 'yyyy-MM-dd');
    const dayExams = examsByDate[dateKey];

    if (dayExams && dayExams.length > 0) {
      onDateSelect(date, dayExams);
    }
  };

  const getDayClasses = (date) => {
    if (!date) return 'invisible';

    const dateKey = format(date, 'yyyy-MM-dd');
    const hasExams = examsByDate[dateKey] && examsByDate[dateKey].length > 0;
    const isDateToday = isToday(date);
    const isPast = isBefore(date, startOfDay(new Date()));

    let classes = 'h-12 w-12 rounded-lg text-sm font-medium transition-all duration-200 flex items-center justify-center ';

    if (isPast) {
      classes += 'text-gray-300 cursor-not-allowed ';
    } else if (hasExams) {
      classes += 'bg-green-100 text-green-800 hover:bg-green-200 cursor-pointer border-2 border-green-200 ';
      if (isDateToday) {
        classes += 'ring-2 ring-primary-500 ';
      }
    } else {
      classes += 'text-gray-400 hover:bg-gray-100 ';
      if (isDateToday) {
        classes += 'bg-primary-50 text-primary-700 ring-2 ring-primary-200 ';
      }
    }

    return classes;
  };

  const weekDays = ['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat'];

  return (
    <div className="bg-white rounded-lg border shadow-sm">
      {/* Calendar Header */}
      <div className="flex items-center justify-between p-4 border-b">
        <button
          onClick={() => navigateMonth(-1)}
          className="p-2 hover:bg-gray-100 rounded-md transition-colors"
          aria-label="Previous month"
        >
          <svg className="w-5 h-5 text-gray-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 19l-7-7 7-7" />
          </svg>
        </button>

        <h2 className="text-lg font-semibold text-gray-900">
          {format(currentDate, 'MMMM yyyy')}
        </h2>

        <button
          onClick={() => navigateMonth(1)}
          className="p-2 hover:bg-gray-100 rounded-md transition-colors"
          aria-label="Next month"
        >
          <svg className="w-5 h-5 text-gray-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 5l7 7-7 7" />
          </svg>
        </button>
      </div>

      {/* Calendar Grid */}
      <div className="p-4">
        {/* Week Days Header */}
        <div className="grid grid-cols-7 gap-2 mb-4">
          {weekDays.map(day => (
            <div key={day} className="text-center text-sm font-medium text-gray-500 py-2">
              {day}
            </div>
          ))}
        </div>

        {/* Calendar Days */}
        <div className="grid grid-cols-7 gap-2">
          {calendarDays.map((date, index) => (
            <button
              key={index}
              onClick={() => handleDateClick(date)}
              className={getDayClasses(date)}
              disabled={!date || isBefore(date, startOfDay(new Date()))}
            >
              {date && format(date, 'd')}
            </button>
          ))}
        </div>

        {/* Legend */}
        <div className="mt-6 pt-4 border-t">
          <div className="flex items-center justify-center gap-6 text-sm">
            <div className="flex items-center gap-2">
              <div className="w-4 h-4 bg-green-100 border-2 border-green-200 rounded"></div>
              <span className="text-gray-600">Available sessions</span>
            </div>
            <div className="flex items-center gap-2">
              <div className="w-4 h-4 bg-gray-100 rounded"></div>
              <span className="text-gray-600">No sessions</span>
            </div>
            <div className="flex items-center gap-2">
              <div className="w-4 h-4 bg-gray-200 rounded"></div>
              <span className="text-gray-600">Past dates</span>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default CalendarView;