import React from 'react';

const CreditAlert = ({ credits, creditBreakdown, mockType, variant = 'info' }) => {
  if (credits === null || credits === undefined || !creditBreakdown) {
    return null;
  }

  const variants = {
    success: {
      container: 'bg-success-50 border-success-200',
      icon: 'text-success-600',
      text: 'text-success-800',
    },
    warning: {
      container: 'bg-warning-50 border-warning-200',
      icon: 'text-warning-600',
      text: 'text-warning-800',
    },
    error: {
      container: 'bg-error-50 border-error-200',
      icon: 'text-error-600',
      text: 'text-error-800',
    },
    info: {
      container: 'bg-primary-50 border-primary-200',
      icon: 'text-primary-600',
      text: 'text-primary-800',
    },
  };

  const styles = variants[variant] || variants.info;

  const getIcon = () => {
    switch (variant) {
      case 'success':
        return (
          <svg className="w-5 h-5" fill="currentColor" viewBox="0 0 20 20">
            <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zm3.707-9.293a1 1 0 00-1.414-1.414L9 10.586 7.707 9.293a1 1 0 00-1.414 1.414l2 2a1 1 0 001.414 0l4-4z" clipRule="evenodd" />
          </svg>
        );
      case 'error':
        return (
          <svg className="w-5 h-5" fill="currentColor" viewBox="0 0 20 20">
            <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zM8.707 7.293a1 1 0 00-1.414 1.414L8.586 10l-1.293 1.293a1 1 0 101.414 1.414L10 11.414l1.293 1.293a1 1 0 001.414-1.414L11.414 10l1.293-1.293a1 1 0 00-1.414-1.414L10 8.586 8.707 7.293z" clipRule="evenodd" />
          </svg>
        );
      case 'warning':
        return (
          <svg className="w-5 h-5" fill="currentColor" viewBox="0 0 20 20">
            <path fillRule="evenodd" d="M8.257 3.099c.765-1.36 2.722-1.36 3.486 0l5.58 9.92c.75 1.334-.213 2.98-1.742 2.98H4.42c-1.53 0-2.493-1.646-1.743-2.98l5.58-9.92zM11 13a1 1 0 11-2 0 1 1 0 012 0zm-1-8a1 1 0 00-1 1v3a1 1 0 002 0V6a1 1 0 00-1-1z" clipRule="evenodd" />
          </svg>
        );
      default:
        return (
          <svg className="w-5 h-5" fill="currentColor" viewBox="0 0 20 20">
            <path fillRule="evenodd" d="M18 10a8 8 0 11-16 0 8 8 0 0116 0zm-7-4a1 1 0 11-2 0 1 1 0 012 0zM9 9a1 1 0 000 2v3a1 1 0 001 1h1a1 1 0 100-2v-3a1 1 0 00-1-1H9z" clipRule="evenodd" />
          </svg>
        );
    }
  };

  // Get credit type names based on mock type
  const getSpecificCreditName = (type) => {
    switch (type) {
      case 'Situational Judgment':
        return 'SJ Credits';
      case 'Clinical Skills':
        return 'CS Credits';
      case 'Mini-mock':
        return 'Mini-Mock Credits';
      default:
        return 'Specific Credits';
    }
  };

  const { specific_credits = 0, shared_credits = 0 } = creditBreakdown;
  const specificCreditName = getSpecificCreditName(mockType);

  return (
    <div className={`border rounded-lg p-4 ${styles.container}`}>
      <div className="flex">
        <div className={`flex-shrink-0 ${styles.icon}`}>
          {getIcon()}
        </div>
        <div className="ml-3">
          <h3 className={`text-sm font-medium ${styles.text}`}>
            {credits > 0 ? 'Credit Verification Successful' : 'Insufficient Credits'}
          </h3>
          <div className={`mt-2 text-sm ${styles.text}`}>
            <div className="space-y-1">
              <p>
                <strong>{specificCreditName}:</strong> {specific_credits}
              </p>
              {mockType !== 'Mini-mock' && (
                <p>
                  <strong>Shared Credits:</strong> {shared_credits}
                </p>
              )}
              <p className="mt-2 font-medium">
                <strong>Total Available for {mockType}:</strong> {credits}
              </p>
            </div>
            {credits > 0 && (
              <p className="mt-2 text-xs opacity-75">
                You have sufficient credits to book this exam.
              </p>
            )}
          </div>
        </div>
      </div>
    </div>
  );
};

export default CreditAlert;